.Footer {
    background-color: black;
    color: white;
    bottom: 0;
    height: 40px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    position: absolute;
}

.Footer p {
    padding: 5px;
}

.Footer a {
    text-decoration: none;
    color: white;
    padding: 5px;
    font-size: 120%
}

@media (max-width: 480px) {
    .Footer {
        height: 58px;
    }
}