.Header h1 {
    font-family: 'Quicksand', sans-serif;
    font-size: 7vw;
    padding: 50px;
}

.Header h3 {
    font-family: 'Quicksand', sans-serif;
    font-size: 140%;
}

#Header-Input {
    width: 70%;
    margin: 10px;
    padding: 30px;
    font-size: 150%;
    border: 3px solid grey; 
    border-radius: 10px;
    line-height: 150%;
}

#Header-Post-Input:focus {
    outline: none;
}

#Header-Input-Button {
    padding: 15px;
    text-align: center;
    width: 30%;
    margin: 0 auto;
    background-color: cornflowerblue;
    border: 2px solid white;
    border-radius: 5px;
    font-size: 120%;
    color: white;
}

#Header-Input-Button:hover {
    background-color: rgb(76, 111, 175);
    cursor: pointer;
}




/* MEDIA QUERIES */
@media (max-width: 1040px) {
    #Header-Input {
        height: 75px;
    }
}

@media (max-width: 680px) {
    #Header-Input {
        height: 95px;
        font-size: 130%;
    }

    .Header h3 {
        font-size: 120%;
    }
}

@media (max-width: 480px) {
    .Header h1 {
        font-size: 9vw;
    }

    #Header-Input {
        font-size: 90%;
        height: 65px;
    }
}