.Post {
    width: 60%;
    margin: 10px auto 60px auto;
    border: 2px solid #636363;
    border-radius: 10px;
    text-align: left;
    padding: 25px;
    display: flex;
    align-items: center;
}

.Post-Vote-Count {
    font-size: 90%;
    padding-right: 15px;
    color:  cornflowerblue;;
}

.Post-Text {
    color: rgb(75, 75, 75);
    padding: 5px;
    font-size: 110%;
}

.Post-Icon-Button {
    border: none;
    background-color: transparent;
}

.Post-Icon-Button:focus {
    outline: none;
}

.Post-Icon {
    vertical-align: middle;
    float: left;
    padding-right: 10px;
    font-size: 200%;
    color:  cornflowerblue;
}

.Post-Icon:hover {
    color:  rgb(76, 111, 175);
    cursor: pointer;
}

.Post-Icon-Clicked {
    vertical-align: middle;
    float: left;
    padding-right: 10px;
    font-size: 200%;
    color:  rgb(76, 111, 175);
}

.Post-Icon-Clicked:hover {
    color:  cornflowerblue;
    cursor: pointer;
}